<template>
	<div id="content">
		<article>
			<div class="contentHeader mb20">
				<h2> </h2>
			</div>
			<div class="row">
				<div class="col-xs-12">
					<div class="menuBox">
						<div>
							<select class="selectStyle2 vertical-middle" style="width:15%" v-model="searchData.searchTextType">
								<option value="cuName"></option>
								<option value="cuid"></option>
								<option value="hpNumber"></option>
							</select>
							<input type="text" class="inputStyle ml10" style="width:44%" v-model="searchData.searchText">
							<h4 class="inline-block ml50" style="width:7%"></h4>
							<select class="selectStyle2" style="width:15%" v-model="searchData.useYn">
								<option value=""></option>
								<option value="Y"></option>
								<option value="N"></option>
							</select>
							<a @click="fnSearch()" class="btnStyle1 float-right"></a>
						</div>
					</div>
				</div>
			</div>

			<div class="row consolMarginTop">
				<div class="col-xs-12">		
					<div class="of_h inline">
						<div class="float-right">
							<a @click="fnRegisterReceiverPop" class="btnStyle2 borderGray mr5" > </a>
							<a @click="fnExcelDownLoad" class="btnStyle2 borderGray mr5" > </a>
							<a @click="fnExcelUpload" class="btnStyle2 borderGray mr5" > </a>
							<input ref="excelFile" type="file" style="display:none;" @change="fnChgInputFile()">
						</div>
					</div>
				
					<!--    -->
					<div class="of_h inline">
						<div class="float-left"> : <span class="color1"><strong>{{totCnt}}</strong></span>
							<SelectLayer @fnSelected="fnSelected"></SelectLayer>
						</div>
					</div>
					
					<div class="row">
						<div class="col-xs-12 consolMarginTop">
							<!-- table -->
							<table  class="table_skin1 bt-000 tbl-striped">
								<colgroup>
									<col style="width:10%">
									<col>
									<col style="width:22%">
									<col style="width:20%">
									<col style="width:12%">
									<col style="width:15%">
								</colgroup>
								<thead>
									<tr>
										<th class="text-center lc-1">No.</th>
										<th class="text-center lc-1"></th>
										<th class="text-center lc-1"> ID</th>
										<th class="text-center lc-1"> </th>
										<th class="text-center lc-1"></th>
										<th class="text-center lc-1 end"></th>
									</tr>
								</thead>
								<tbody>
									<tr v-for="(data) in items" :key="data.cuInfoId">
										<td class="text-center"> {{ data.rownum + offset}} </td>
										<td class="text-left"> {{ data.cuName }} </td>
										<td class="text-left"> {{ data.cuid }} </td>
										<td class="text-center"> {{ data.hpNumber }} </td>
										<td v-if="data.useYn == 'Y'" class="text-center">  </td>
										<td v-else class="text-center">  </td>
										<td class="end">
											<a @click="fnModifyReceiverPop(data)" class="btnStyle1 borderLightGray small mr5"></a>
											<a @click="fnDeleteReceiver(data)" class="btnStyle1 borderLightGray small mr5"></a>
										</td>
									</tr>	
									<tr v-if="items.length == 0">
										<td class="text-center"></td>
										<td class="text-center" colspan="6">  .</td>
									</tr>	
								</tbody>
							</table>
						</div>	
					</div>
				</div>			
			</div>
			<!-- pagination Start -->
			<div id="pageContent">
				<PageLayer @fnClick="fnPageClick" :listTotalCnt="totCnt" :selected="listSize" :pageNum="pageNo" ref="updatePaging"></PageLayer>
			</div>
			<!-- pagination End-->
			<footer>CopyrightLG Plus Corp. All Rights Reserved.</footer>
		</article>

		<!--  Modal -->
		<RcvrRegMdfyLayer :rowData="rowData" :status.sync="status"></RcvrRegMdfyLayer>
	</div>
</template>

<script>
import addressApi from '../service/addressApi'
//import tokenSvc from '@/common/token-service';
import confirm from "@/modules/commonUtil/service/confirm";
import PageLayer from '@/components/PageLayer.vue';
import SelectLayer from '@/components/SelectLayer.vue';
import RcvrRegMdfyLayer from '../components/bc-receiver-register.vue';
import {eventBus} from "@/modules/commonUtil/service/eventBus";

export default {
	name: "receiverManageList",
	components: {
		SelectLayer,
		PageLayer,
		RcvrRegMdfyLayer,
	},
	props: {
		searchData : {
			type: Object,
			require: false,
			default: function() {
				return {
					'cuName'		: '',
					'hpNumber'		: '',
					'cuId'			: '',
					'searchTextType': 'cuName',
					'searchText'	: '',
					'useYn'			: '',
				}
			}
		}
	},
	data() {
		return {
			listSize : 10,	// select  value (  )
			pageNo : 1,		//   
			totCnt : 0,		//  
			offset : 0,		 // 
			items: [],
			status: '',
			rowData: {},
			cuInfoId: -1,
		}
	},
	mounted() {
		this.fnSearch();
	},
	methods: {
		fnSearch(pageNum) {
			this.pageNo = (this.$gfnCommonUtils.defaultIfEmpty(pageNum, '1'))*1;
			this.fnSearchReceiverList();
		},
		// select    
		fnSelected(listSize) {
			this.listSize = Number(listSize);
			this.$refs.updatePaging.fnAllDecrease();
		},
		//  
		async fnSearchReceiverList(){
			var params = Object.assign({}, this.searchData);
			params.pageNo = this.pageNo;
			params.listSize = this.listSize;

			await addressApi.selectReceiverList(params).then(response =>{
				var result = response.data;
				if(result.success) {
					this.items = result.data;
					this.totCnt = result.pageInfo.totCnt;
					this.offset = result.pageInfo.offset;
				} else {
					confirm.fnAlert("", result.message);
				}
			});
		},
		// 
		fnDeleteReceiver(data) {
			this.cuInfoId = data.cuInfoId;
			eventBus.$on('callbackEventBus', this.fnProcDeleteReceiver);
			confirm.fnConfirm('', "  ?", "");
		},
		fnProcDeleteReceiver() {
			let params = {
				cuInfoId: this.cuInfoId
			};

			addressApi.deleteReceiver(params).then(response =>{
				const result = response.data;
				if(result.success) {
					confirm.fnAlert('', '.');
					this.cuInfoId = -1;
					this.fnSearchReceiverList();
				} else {
					confirm.fnAlert('', result.message);
				}
			});
		},
		//  
		fnModifyReceiverPop(data) {
			this.status = 'U';
			this.rowData = data
			jQuery("#RcvrRegMdfyLayer").modal("show");
			
		},
		//  
		fnRegisterReceiverPop() {
			this.status = 'R';
			this.rowData = {};
			jQuery("#RcvrRegMdfyLayer").modal("show");
		},
		fnPageClick(pageNo) {
			this.pageNo = pageNo;
			this.fnSearchReceiverList();
		},
		// 
		fnExcelDownLoad() {
			var params = this.searchData;
			addressApi.excelDownloadReceiverTemplate(params);
		},
		// 
		fnExcelUpload() {
			this.$refs.excelFile.click();
		},
		//  
		fnValidExcel() {
			const uploadFile = this.$refs.excelFile;
			if(uploadFile.value == 0) {
				confirm.fnAlert(this.componentsTitle, ' .');
				return false;
			}
			const permitExten = 'xls,xlsx'.split(',');
			const extnIdx = uploadFile.value.lastIndexOf('.');
			const extn = uploadFile.value.substring(extnIdx+1);
			if((permitExten.indexOf(extn) < 0)) {
				confirm.fnAlert(this.componentsTitle, '  .');
				return false;
			}
		},
		async fnChgInputFile() {
			this.fnValidExcel();
			const uploadFile = this.$refs.excelFile;
			console.log('uploadFile.value : ' + uploadFile.value);

			let fd = new FormData();
			fd.append('file', this.$refs.excelFile.files[0]);
			//const vmSelectLayer = this;
			await addressApi.excelUploadReceiver(fd).then(response =>{
				const result = response.data;
				if(result.success) {
					confirm.fnAlert(this.componentsTitle, '.');
				} else {
					confirm.fnAlert('', result.message);
				}
			});
		},
	}
}
</script>


