<template>
  <div class="modal fade modalStyle in" id="corpInfoPopup" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-body">
					<div class="of_h">
						<h2> </h2>
						<hr>
						<h4> </h4>
						<div class="border-line mt20 Dashboard06">
							<div class="of_h">
								<h5 class="inline-block" style="width:20%"> <span class="color1">*</span></h5>
								<h5 style="width:80%" class="float-right">{{ regno }}</h5>
							</div>	
						
							<div class="of_h consolMarginTop">
								<h5 class="inline-block" style="width:20%"> <span class="color1">*</span></h5>
								<input type="text" class="inputStyle float-right" style="width:80%" title=" " v-model="corpName">
							</div>	
							<div class="of_h consolMarginTop">
								<h5 class="inline-block" style="width:20%"> <span class="color1">*</span></h5>
								<input type="text" class="inputStyle float-right" style="width:80%" title=" " v-model="ceoName">
							</div>
							<div class="of_h consolMarginTop">
								<h5 class="inline-block" style="width:20%"> <span class="color1">*</span></h5>
								<input type="text" class="inputStyle" style="width:31%" title=" "  v-model="busitype">
								<h5 class="inline-block ml20" style="width:10%"> <span class="color1">*</span></h5>
								<input type="text" class="inputStyle float-right" style="width:31%" title=" " v-model="busiclass">
							</div>	
							<div class="of_h consolMarginTop">
								<h5 class="inline-block" style="width:20%"> <span class="color1">*</span></h5>
								<input type="text" class="inputStyle" style="width:58%" title=" "  v-model="woplaceAddress">
								<a href="#self" class="btnStyle1 backLightGray float-right" title=""></a>
								<input type="text" class="inputStyle consolMarginTop float-right" style="width:80%" title="  " v-model="woplaceAddressDetail" placeholder="">
							</div>
							<div class="of_h consolMarginTop">
								<h5 class="inline-block" style="width:20%"></h5>
								<input type="text" class="inputStyle float-right" style="width:80%" title=" " v-model="wireTel" placeholder="- ">
							</div>
							<div class="of_h consolMarginTop">
								<h5 class="inline-block" style="width:20%"> <span class="color1">*</span></h5>
								<input type="text" class="inputStyle" style="width:37%" title=" ">
								<a @click="fnUploadCorpImg"  class="btnStyle1 backLightGray ml15" title=""></a>
                <a @click="fnDownloadCorpImg" class="btnStyle1 backLightGray float-right" title=""></a>
								<p class="mt10 lc-1 Modaltext2" style="margin-left:20%"><i class="far fa-info-circle"></i>PDF, JPG, JPEG, PNG  . (: 5MB)</p>
							</div>
						</div>

						<h4> </h4>
						<div class="border-line mt20 Dashboard06">
							<div class="of_h">
								<h5 class="inline-block" style="width:20%"> </h5>
								<h5 class="inline-block" style="width:55%">{{ domainName }}</h5>
							</div>	
						</div>						
					</div>
					<div class="text-center mt40">
						<a @click="fnSave" class="btnStyle2 backBlack" title=""></a>
						<a @click="fnCloseLayer" class="btnStyle2 backWhite ml10" title=""></a>
					</div>
				</div>
      </div>
    </div>
  </div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm.js";
import {eventBus} from "@/modules/commonUtil/service/eventBus";
import myPageApi from '@/modules/myPage/service/myPageApi';

export default {
  name: 'corpInfoPopup',
  data() {
    return {
      regno : '',                   // 
      corpName : '',                // 
      ceoName : '',                 // ceo
      busitype : '',                // 
      busiclass : '',               // 
      woplaceAddress : '',          //
      woplaceAddressDetail : '',    // 
      wireTel : '',                 // 
      domainName : ''               //  
    }
  },
  props: {
    corpInfo : {
      type : Object,
      require : true
    },
    popReset: {
      type: Number
    }
  },
  mounted() {
    this.fnReset();
  },
  watch: {
    popReset(){
      this.fnReset();
    },
    wireTel(){
      if(this.wireTel != undefined){
        return this.wireTel = this.wireTel.replace(/[^0-9]/g, '');
      }
    }
  },
  methods: {
      fnCloseLayer(){
          jQuery("#corpInfoPopup").modal("hide");
      },
      //  
      fnReset(){
        this.regno = this.corpInfo.regno;
        this.corpName = this.corpInfo.corpName;
        this.ceoName = this.corpInfo.ceoName;
        this.busitype = this.corpInfo.busitype;
        this.busiclass = this.corpInfo.busiclass;
        this.woplaceAddress = this.corpInfo.woplaceAddress;
        this.woplaceAddressDetail = this.corpInfo.woplaceAddressDetail;
        this.wireTel = this.corpInfo.wireTel;
        this.domainName = this.corpInfo.domainName;
      },
      // 
      fnUploadCorpImg(){

      },
      // 
      fnDownloadCorpImg(){

      },
      // 
      fnSave(){
        if(this.corpName == undefined ||this.corpName == ''){
          confirm.fnAlert("", "  .");
          return;
        }
        if(this.ceoName == undefined ||this.ceoName == ''){
          confirm.fnAlert("", "  .");
          return;
        }
        if(this.busitype == undefined ||this.busitype == ''){
          confirm.fnAlert("", "  .");
          return;
        }
        if(this.busiclass == undefined ||this.busiclass == ''){
          confirm.fnAlert("", "  .");
          return;
        }
        if(this.woplaceAddress == undefined ||this.woplaceAddress == ''){
          confirm.fnAlert("", "   .");
          return;
        }
        if(this.woplaceAddressDetail == undefined ||this.woplaceAddressDetail == ''){
          confirm.fnAlert("", "   .");
          return;
        }
        //  

        eventBus.$on('callbackEventBus', this.fnSaveCallBack);
        confirm.fnConfirm( "  ", "?", "");
      },
      fnSaveCallBack(){
        var params = {
          corpId : this.corpInfo.corpId,
          corpName : this.corpName,
          ceoName : this.ceoName,
          busitype : this.busitype,
          busiclass : this.busiclass,
          woplaceAddress : this.woplaceAddress,
          woplaceAddressDetail : this.woplaceAddressDetail,
          wireTel : this.wireTel
        }

        myPageApi.saveCorpInfo(params).then(response =>{
          var result = response.data;
          if(result.success) {
            confirm.fnAlert( ".", "");
            this.fnCloseLayer();
          } else {
            confirm.fnAlert(result.message, "");
          }
        });
      }
  }
}
</script>
