<template>
  <div class="modal fade modalStyle in" id="myPagePopup" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-body">
					<div class="of_h">
						<h2></h2>
						<hr>
						<div class="of_h">
							<h5 class="inline-block" style="width:20%">ID</h5>
							<h5 class="inline-block float-right" style="width:80%">{{ loginId }}</h5>
						</div>	
					
						<div class="of_h consolMarginTop">
							<h5 class="inline-block" style="width:20%"></h5>
							<input type="text" class="inputStyle float-right" style="width:80%" title=" "  v-model="userName">
						</div>	
						<div class="of_h consolMarginTop">
							<h5 class="inline-block" style="width:20%"></h5>
							<h5 class="inline-block" style="width:53%"> : {{ pwdUpdDt }}</h5>
							<a class="btnStyle1 backLightGray float-right width120" title=" " @click="fnChgPwdDiv"> </a>
						</div>
            <div id="chgPwdDiv" style="display:none;">
              <div class="of_h consolMarginTop">
                <h5 class="inline-block" style="width:20%"> </h5>
                <div class="float-right" style="width:80%">
                  <input type="password" class="inputStyle" title="  " v-model="loginPwd" placeholder=" ">
                  <input type="password" class="inputStyle consolMarginTop" title="   " v-model="chkLoginPwd" placeholder="  ">
                </div>
              </div>
            </div>
            <div class="of_h consolMarginTop">
              <h5 class="inline-block" style="width:20%"> </h5>
              <input type="text" class="inputStyle" style="width:55%" title="  "  v-model="hpNumber" :disabled="true">
              <a href="#self" class="btnStyle1 backLightGray float-right width120" title=" "  @click="fnChgHpNumDiv"> </a>
            </div>
            <div id="fnChgHpNumDiv" style="display:none;">
              <div class="of_h consolMarginTop">
                <h5 class="inline-block" style="width:20%">  </h5>
                <input type="text" class="inputStyle" style="width:55%" title="   " v-model="chgHpNumber" placeholder="- ">
                <button @click="fnGetCertifyNumber" id="certifyBtn" class="btnStyle1 backLightGray float-right width120" title=" ">{{ certifyMsg }}</button>
              </div>
              <div class="of_h consolMarginTop">
                <h5 class="inline-block" style="width:20%"></h5>
                <input type="text" class="inputStyle float-right" style="width:80%" title=" " placeholder=" " v-model="certifyNumber">
              </div>
            </div>
						<div class="of_h mt30">
							<div class="text-center">
								<a @click="fnSave" class="btnStyle1 backBlack" title=""></a>
								<a @click="fnCloseLayer" class="btnStyle1 backWhite ml10" title=""></a>
							</div>
						</div>
					</div>					
				</div>
      </div>
    </div>
  </div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm.js";
import {eventBus} from "@/modules/commonUtil/service/eventBus";
import myPageApi from '@/modules/myPage/service/myPageApi';

export default {
  name: 'myPagePopup',
  data() {
    return {
      loginId    : '',
      userName  : '',
      pwdUpdDt  : '',
      hpNumber  : '',
      loginPwd  : '',
      chkLoginPwd : '',
      chgHpNumber : '',
      certifyNumber  : '',
      certifyMsg : ' '
    }
  },
  props: {
    memberInfo : {
      type : Object,
      require : true
    },
    popReset: {
      type: Number
    }
  },
  mounted() {
    this.fnReset();
  },
  watch: {
    popReset() {
      //     display 
      jQuery("#chgPwdDiv").css("display", "none");
      //     display 
      jQuery("#fnChgHpNumDiv").css("display", "none");
      //    
      jQuery("#certifyBtn").prop("disabled", false);
      this.fnReset();
    },
    chgHpNumber(){
      return this.chgHpNumber = this.chgHpNumber.replace(/[^0-9]/g, '');
    }
  },
  methods: {
      fnCloseLayer(){
        jQuery("#myPagePopup").modal("hide");
      },
      //  
      fnReset(){
        this.loginId  = this.memberInfo.loginId;
        this.hpNumber = this.memberInfo.hpNumber;
        this.pwdUpdDt = this.memberInfo.pwdUpdDt;
        this.userName = this.memberInfo.userName;
        this.certifyMsg = " ";
        
        //  ,  
        this.chgHpNumber  = '';
        this.certifyNumber   = '';
        this.loginPwd     = '';
        this.chkLoginPwd  = '';
      },
      //    
      fnChgHpNumDiv(){
        if(jQuery("#fnChgHpNumDiv").is(":visible")){
          this.chgHpNumber  = '';
          this.certifyNumber   = '';
          jQuery("#fnChgHpNumDiv").css("display", "none");
        } else {
          jQuery("#fnChgHpNumDiv").css("display", "block");
        }
      },
      //    
      fnChgPwdDiv(){
        if(jQuery("#chgPwdDiv").is(":visible")){
          this.loginPwd     = '';
          this.chkLoginPwd  = '';
          jQuery("#chgPwdDiv").css("display", "none");
        } else {
          jQuery("#chgPwdDiv").css("display", "block");
        }
      },
      //    
      fnGetCertifyNumber(){
        if(this.chgHpNumber.trim().length == 0){
            confirm.fnAlert("", "   .");
            return;
        }

        this.certifyMsg = " .."
      },
      // 
      fnSave(){
        if(this.loginPwd.trim().length > 0){
          if(this.loginPwd != this.chkLoginPwd){
            confirm.fnAlert("", "   .");
            return;
          }
        }

        if(this.chgHpNumber.trim().length > 0){
          if(this.certifyNumber.trim().length == 0){
            confirm.fnAlert("", " .");
            return;
          }
        }

        eventBus.$on('callbackEventBus', this.fnSaveCallBack);
        confirm.fnConfirm( "  ", "?", "");
      },
      fnSaveCallBack(){
        var params = {
          loginPwd : this.loginPwd.trim(),
          hpNumber : this.chgHpNumber.trim(),
          certifyNumber : this.certifyNumber.trim()
        }

        myPageApi.saveMemberInfo(params).then(response =>{
          var result = response.data;
          if(result.success) {
            confirm.fnAlert( ".", "");
            this.fnCloseLayer();
          } else {
            confirm.fnAlert(result.message, "");
          }
        });
      }
  }
}
</script>
